import 'assets/css/qrCertificationHistory.css';
import 'react-datepicker/dist/react-datepicker.css';
import { Grid } from 'gridjs-react';
import 'gridjs/dist/theme/mermaid.min.css';
import { data, gridColumn } from 'app/data/deviceManagement/stations/gridData';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';

function Roles() {
  const navigate = useNavigate();
  const groupList = ['사용', '미사용'];
  const [group, setGroup] = useState('');

  const gridRowClick = (e) => {
    const rowObj = {};
    if (e.target.getAttribute('data-column-id') !== null) {
      e.target.parentNode.childNodes.forEach((obj) => {
        rowObj[`${obj.getAttribute('data-column-id')}`] = obj.textContent;
      });
      rowObj.detailLink = [{ title: '수정', link: '/administrator/roles/addOrUpdate' }];
      navigate(rowObj.detailLink[0].link, {
        state: rowObj,
      });
    }
  };

  return (
    <div className="section__grid__wrap content qr__history">
      <section className="qr__history__search">
        <div className="section__inner">
          <div className="search__wrap groups">
            <div className="left">
              <div className="selector__wrap">
                <dl>
                  <dt>사용여부</dt>
                  <dd>
                    <select className="custom__select" onChange={(e) => setGroup(e.target.value)}>
                      <option value="">전체</option>
                      {groupList.map((obj, idx) => (
                        <option key={obj} value={obj}>
                          {obj}
                        </option>
                      ))}
                    </select>
                  </dd>
                </dl>
              </div>
            </div>
            <button
              className="custom__btn add__btn"
              onClick={() => {
                navigate('/administrator/roles/addOrUpdate', {
                  state: {
                    detailLink: [{ title: '추가', link: '/administrator/roles/addOrUpdate' }],
                  },
                });
              }}
            >
              <svg fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 6v6m0 0v6m0-6h6m-6 0H6"
                />
              </svg>
              <span className="f__medium">추가</span>
            </button>
          </div>
          <div
            className="grid__wrap"
            onClick={(e) => {
              gridRowClick(e);
            }}
          >
            <Grid
              sort="true"
              className="grid"
              data={data}
              columns={gridColumn}
              pagination={{
                enabled: true,
                limit: 10,
              }}
            />
          </div>
        </div>
      </section>
    </div>
  );
}

export default Roles;
